import axios from "axios";
import router from '@/router';
import AuthHelper from './helper/AuthHelper';
const url = '/api/users/';

class UserService{
    // Get User
    static async getUsers(limit, search, page){
        let params = {
            params: {
                limit: limit,
                page: page,
                search: search
            }
        };
        if(search.length <= 0){
            delete params.params.search;
        }
        let header = {...AuthHelper.getHeader(), ...params};
        return axios.get(url, header);
    }
    // Create User
    static async insertUser(formData){
        let datas = {
            data: formData
        }
        let header = {...AuthHelper.getHeader()};
        return axios.post(url, formData, header);
    }

    // Delete User
    static deleteUser(id){
        let header = {...AuthHelper.getHeader()};
        return axios.delete(`${url}${id}`, header);
    }

    // Update User
    static updateUser(id, formData){
        let datas = formData;
        datas['_id'] = id;
        let header = {...AuthHelper.getHeader()};
        return axios.put(url, datas, header);
    }
    

    // Get user
    static async getUser(id){
        let header = {...AuthHelper.getHeader(), validateStatus: function (status) {
            return status < 500;
          }};
        return axios.get(`${url}${id}`, header);
    }
}

export default UserService;