<template>
  <TableForm ref="tf_user" :key="k" :formDetails="formDetails" :columnList="columnList" @processData="processData" />
</template>

<script>
import UserService from '../../services/UserService'
import CommonHelper from '../../services/helper/CommonHelper'
import TableForm from '../custom/TableForm'
export default {
  name: 'User',
  components:{
    TableForm
  },
  data () {
    return {
      formDetails:{
        title: ((this.$router.currentRoute.path).includes("create")) ? 'Create user' : 'Edit user',
        btnSubmitText: ((this.$router.currentRoute.path).includes("create")) ? 'Create' : 'Update',
        submitUrl: '/api/user/register'
      },
      columnList:[
        {
          type: 'input',
          datatype: 'text',
          label: 'Name',
          prepend: '@',
          append: '',
          description: 'This is username',
          dbColumn: 'name',
          value: '',
          disabled: false
        },
        {
          type: 'input',
          datatype: 'email',
          label: 'Email',
          prepend: '',
          append: '',
          description: 'This is email',
          dbColumn: 'email',
          value: '',
          disabled: false
        },
        {
          type: 'input',
          datatype: 'password',
          label: 'Password',
          prepend: '',
          append: '',
          description: 'This is password',
          dbColumn: 'password',
          value: '',
          disabled: false
        },
      ],
      k: 0,
      usersOpened: null
    }
  },
  created(){
    if ( !(this.$router.currentRoute.path).includes("create") )
      this.getUserDetail();
  },
  methods: {
    processData(formData){
      if ((this.$router.currentRoute.path).includes('create')){
        this.insertUser(formData);
      }else{
        this.editUser(formData);
      }
    },
    async insertUser(formData){
      try{
        let InsertUserDatas = UserService.insertUser(formData);
        InsertUserDatas.then( (res) =>{
          this.$refs.tf_user.showSuccessAlert('Success');
        })
        .catch((error) =>{
            this.$refs.tf_user.showErrorAlert(error.response.data);
        });
      }catch(error) {
        console.log(error);
      };
    },
    async editUser(formData){
      try{
        let UpdateUserDatas = UserService.updateUser(this.$route.params.id, formData);
        UpdateUserDatas.then( (res) =>{
          this.$refs.tf_user.showSuccessAlert('Success');
        })
        .catch((error) =>{
            this.$refs.tf_user.showErrorAlert(error.response.data);
        });
      }catch(error) {
        console.log(error);
      };
    },
    async getUserDetail(){
      try{
        let userData = UserService.getUser(this.$route.params.id);
        userData.then( (res) =>{
          let userDetailObj = res.data;
          for(const key in userDetailObj){
            let columnKey = CommonHelper.getObjectKeyByValue(this.columnList, 'dbColumn', key);
            if( columnKey !== false ){
              this.columnList[columnKey]['value'] = userDetailObj[key];
            }
          }
          this.$refs.tf_user.initFormData();
        })
        .catch((error) =>{
          console.log(error)
          this.$refs.tf_user.showErrorAlert(error.response);
        })
      }catch(error) {
        console.log(error);
      };
    },
    goBack() {
      this.usersOpened ? this.$router.go(-1) : this.$router.push({path: '/users'})
    }
  }
}
</script>
